import Caption from './Caption'
import SpiceyLink from './SpiceyLink'
export default function(props){
    const height = props.height?props.height:null
    return(<>
        <header class="masthead" style={{backgroundImage: `url('${props.imgSrc}')`,height}}>
            <div class="overlay"></div>
            <div class="container">
            <div class="row">
                <div class="col-lg-8 col-md-10 mx-auto">
                <div class="post-heading" style={{marginBottom:"3p"}}>
                    <h1>{props.title}</h1>
                    <h2 class="subheading">{props.subtitle}</h2>
                </div>
                </div>
            </div>
            
            </div>
        </header> 
        <div class="container" style={{marginTop:"-50px",marginBottom:"50px"}}>
            <div class="row">
                <div class="col-lg-8 col-md-10 mx-auto">
    <Caption>Image courtesy of <em><SpiceyLink noDec noIFrame href={props.src}>{props.srcName}</SpiceyLink></em></Caption>
                </div>
            </div>
        </div>
    </>)
}