export default function(props){
    if((Number(props.level)&&Number(props.level)==0)||0==props.level)
        return(<h1 className="display-4 text-center">{props.children}</h1>)
    if((Number(props.level)&&Number(props.level)==1)||1==props.level)
        return(<h1>{props.children}</h1>)
    if((Number(props.level)&&Number(props.level)==2)||2==props.level)
        return(<h2>{props.children}</h2>)
    if((Number(props.level)&&Number(props.level)==3)||3==props.level)
        return(<h3>{props.children}</h3>)
    if((Number(props.level)&&Number(props.level)==4)||4==props.level)
        return(<h4>{props.children}</h4>)
    return(<h2>{props.children}</h2>)
}