import React, { useState } from 'react';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";

export default function(props){
    const [over, setOver] = useState(false);
    const [mouse, setMouse] = useState({x:0,y:0});
    let _onMouseMove = (e) => {
        setMouse({ x: e.clientX+20, y: window.innerHeight/2>e.clientY?e.clientY+20:e.clientY-height-20 });
    }
    const width=props.width?props.width:500
    const height = props.noIFrame?0:(props.height?props.height:500)
    const bWidth = 0
    const noIFrame = props.noIFrame?props.noIFrame:false;
    return(
        <>
            {props.sameSite?<Link to={props.href} onMouseMove={_onMouseMove.bind(this)} onMouseOver={()=>setOver(true)} onMouseLeave={()=>setOver(false)} style={props.noDec?{textDecoration:"none"}:{textDecoration: "underline", textDecorationColor: "blue", color:"blue"}}>{props.children}</Link>:<a href={props.href} onMouseMove={_onMouseMove.bind(this)} onMouseOver={()=>setOver(true)} onMouseLeave={()=>setOver(false)} style={props.noDec?{textDecoration:"none"}:{textDecoration: "underline", textDecorationColor: "blue", color:"blue"}} target="_blank" rel="noopener noreferrer">{props.children}</a>}
            <div style={{
                display: over?"block":"none",
                width: width,
                position:"fixed",
                zIndex:9999,
                'top':mouse.y,
                'left':mouse.x,
                borderRadius:10,
                backgroundColor:"#dbdbdb",
                border:"solid",
                borderWidth:bWidth,
                padding:0,margin:0
            }} >
                <p style={{textAlign:"center",padding:0,margin:0}}>{props.text}</p>
                {noIFrame||window.location.href.includes("preview=true")||window.navigator.userAgent == 'ReactSnap'?null:<iframe loading="lazy" scrolling="no" src={props.sameSite?"/free-speech"+props.href+"?preview=true":props.href} style={{borderRadius:10, border:"none",padding:0,margin:0,overflow:"hidden!important",zIndex:9999,position:"relative"}} width = {width-bWidth*2} height = {height-bWidth*2}>
                </iframe> }
            </div>
        </>
    )
}