import SpiceyLink from './SpiceyLink'
import SubTitle from './SubTitle'
import NormalText from './NormalText'
import Quote from './Quote'
import InArticleImage from './InArticleImage'
export default function(){
    return(
        <>
            <div class="row">
            <div className="col-4">
                <InArticleImage src="https://static.dw.com/image/43286160_304.png" site="https://www.dw.com/en/washington-sues-facebook-over-cambridge-analytica-scandal/a-46811290"/>
            </div>
            <div className="col-8">
                <NormalText style={{marginTop:0}}>
                    The Facebook–Cambridge Analytica data scandal was a prime example of how people shape the policies of social media sites. After the scandal, the #deleteFacebook hashtag was trending and Facebook users demanded change. As a result, Facebook made numerous changes, which were documented in this <SpiceyLink href="https://techcrunch.com/2018/04/10/how-facebook-has-reacted-since-the-data-misuse-scandal-broke/">TechCrunch article</SpiceyLink>. These measures included making privacy settings easier to modify, certifying marketer on the platform, informing users whose data was misused and gauging their impact on politics. Clearly, Facebook heard the complaints of its users and changed for the better.
                </NormalText>
            </div>
            </div>
            <NormalText>
                Another thing to note is, social media sites are fundamentally different from public spaces and shouldn’t have the same required protections. If I would like to spread political information in person, my only practical option might be a public space and thus it makes sense to provide protections in those places. However, on the internet, there is no limit to the sites I can use to express my opinion. Even if you are somehow shut away from every single site on the internet. You could always just create your own. The internet has virtually no barrier to entry and therefore will provide a way in which you can express your views.
            </NormalText>
            <Quote>The founding fathers knew what they were doing.</Quote>
            <div class="row">
            <div className="col-8">
                <NormalText>
                    The founding fathers rightly limited the scope of the first amendment to only protect individuals from the government. They worded the first amendment to allow a private entity to moderate its own content. In their time, a prime example would be a newspaper publisher choosing which pieces get published. Various publishers would have various policies on what they allowed. If a given author had an issue with their current publisher’s policies, they could always switch publishers. 
                </NormalText>
            </div>
            <div className="col-4">
                <InArticleImage src="https://media.nationalgeographic.org/assets/photos/138/127/67ea52ea-9756-4a44-ae02-d398261084df.jpg" site="https://www.nationalgeographic.org/article/founding-fathers/"/>
            </div>
            </div>

            <NormalText>
                This system continues to work for modern-day publishers. Take a major publisher like Penguin Random House as an example, they heavily emphasize their <SpiceyLink href="https://authornews.penguinrandomhouse.com/protecting-our-authors-right-to-freedom-of-speech-and-expression/">commitment</SpiceyLink> to protecting their author’s freedom of speech rights. Clearly, our current system reflects the will of the authors.
            </NormalText>
            <NormalText>
                The internet functions similarly, with a social media site dictating what can and cannot be published on their service. If any problems regarding free speech on a platform exist, it is because the authors (the “users” in this case) did not take issue with it.
            </NormalText>
        </>
    )
}