import SpiceyLink from './SpiceyLink'
import SubTitle from './SubTitle'
import NormalText from './NormalText'
import Quote from './Quote'
import InArticleImage from './InArticleImage'
export default function(){
    return(
        <>
            <NormalText>
                In order to understand why it is helpful to look at a close physical analog to social media: newspapers. Newspaper publishers are protected by the first amendment’s “Freedom of the press” cause and can publish anything they want to. However, a key thing to note is that they can still be held liable for their content. That is why someone like <SpiceyLink href="https://www.cbc.ca/news/entertainment/judge-rule-monday-johnny-depp-libel-case-1.5778460">Johnny Depp can sue a newspaper for libel</SpiceyLink>. Overall, this makes a great system, the news doesn’t get censored but newspapers can still be held accountable.
            </NormalText>
            <div class="row">
            <div className="col-4">
                <InArticleImage src="http://www.coreypadveen.com/wp-content/uploads/2015/10/COMP007.jpg" site="http://www.coreypadveen.com/case-study-the-changing-landscape-of-news-and-media-outlets/"/>
            </div>
            <div className="col-8">
                <Quote>Social media sites are not newspapers.</Quote>
                <NormalText>
                    The issue is, this system breaks down if it is used for social media sites. This would turn every social media site into a publisher and make them responsible for anything their users post. While it might make sense to punish a platform for allowing certain content to be posted (think Napster), it would also require platforms to check every piece of content that enters their site. If we look at YouTube as an example, with its 20 hours of video uploaded every hour. It's clear that it would be impossible for social media sites to do this. 
                </NormalText>
            </div>
            </div>

            <NormalText>
                It was for this reason that Section 230 of the Communications Decency Act was written. It says that <SpiceyLink href="https://www.eff.org/issues/cda230" noIFrame text={`Article from the Electionic Frontier Foundation titled "Section 230 of the Communications Decency Act"`}>"No provider or user of an interactive computer service shall be treated as the publisher or speaker of any information provided by another information content provider."</SpiceyLink> This wording made social media companies exempt from liability over user-generated content and avoided the problems described above.
            </NormalText>
            <div class="row"><div className="col-8">
                <NormalText>  
                    With this legal freedom, social media companies designed content reporting systems for false information. Some companies like YouTube even worked out deals with music publishers to settle copyright disputes out of court. It's clear that the industry is more than capable of regulating itself and it functions best when the government is uninvolved. The fast-paced always changing nature of the internet is just not compatible with the slow-moving and static legal system. As the meme rightly claims, <strong>modern problems require modern solutions</strong>. We need to leave protecting freedom of speech in the hands of companies. If their customers want free speech, the social media giants will respond with innovative and effective solutions.
                </NormalText>
            </div>
            <div className="col-4">
                <InArticleImage src="https://i.kym-cdn.com/photos/images/newsfeed/001/697/798/be2.jpg" site="https://knowyourmeme.com/photos/1697798-modern-problems-require-modern-solutions"/>
            </div></div>
        </>
    )
}