
import React from 'react';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";
import ReactTooltip from 'react-tooltip';

export default class Header extends React.Component {
    constructor(props){
        super(props)
        console.log("NEW")
        this.state={
            alpha:true?window.scrollY/window.innerHeight:1
        }
    }
    componentWillReceiveProps(newProps){
        console.log(newProps.active + " - "+this.props.active)
        this.setState({alpha:true?window.scrollY/window.innerHeight:1})
    }
    componentDidMount(){
        window.addEventListener('scroll',this.scrolled)
    }
    componentWillUnmount(){
        window.removeEventListener('scroll',this.scrolled)
    }
    scrolled = () => {
        if(true)
            this.setState({alpha:window.scrollY/window.innerHeight})
    }

    render(){
        console.log(this.props.active)
        return(
            <>
            <div>
                {false && <div>
                    <br/><br/><br/>
                </div>}
                <nav className={`navbar navbar-expand-lg fixed-top navbar-dark`} style={{backgroundColor: "rgba(0,0,0,"+this.state.alpha+")"}}>
                    <a className="navbar-brand">Free Speech On The Web</a>
                    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
                        <div className="navbar-nav ml-auto">
                            <li class="nav-item">
                                <Link to="/" data-tip="Background info + intro to main viewpoints" className={`nav-item nav-link ${this.props.active == "Home"?"active":""}`}>Intro</Link>
                            </li>
                            <li class="nav-item">
                                <Link to="/the-people" className={`nav-item nav-link ${this.props.active == "the-people"?"active":""}`} class="nav-link" data-tip="People need to demand better protection on social media">The People</Link>
                            </li>
                            <li class="nav-item">
                                <Link to="/the-companies" className={`nav-item nav-link ${this.props.active == "the-companies"?"active":""}`} data-tip="Social media companies must take action">The Companies</Link>
                            </li>
                            <li class="nav-item">
                                <Link to="/the-government" className={`nav-item nav-link ${this.props.active == "the-government"?"active":""}`} data-tip="The government must take action">The Government</Link>
                            </li>
                            <li class="nav-item">
                                <Link to="/further-reading" className={`nav-item nav-link ${this.props.active == "further-reading"?"active":""}`} data-tip="Sources you can read for more info">Further Reading</Link>
                            </li>
                        </div>
                    </div>
                </nav>
            </div>
                        <ReactTooltip backgroundColor="blue"/>
                        </>
        )
    }
}

