import SpiceyLink from './SpiceyLink'
import SubTitle from './SubTitle'
import NormalText from './NormalText'
import InArticleImage from './InArticleImage'

export default function Intro(){
    return(
        <>
            
            <div class="row">
            <div className="col-4">
                <InArticleImage src="https://static.stacker.com/s3fs-public/styles/properly_sized_image/s3/00000211_5.png" site="https://stacker.com/stories/3285/how-americans-use-internet-today-numbers"/>
            </div>
                <div className="col-8">
                <SubTitle level={1}>Americans and the internet</SubTitle>
                <NormalText>
                    If you are reading this, then you probably understand what the internet is and how to use it. You're not alone in this, according to the Pew Research Center, <SpiceyLink href="https://www.pewresearch.org/internet/fact-sheet/internet-broadband/">90% of Americans use the internet in 2019</SpiceyLink>. Perhaps most relevant to the issue, the Pew Research Center also found that in 2018 <SpiceyLink href="https://www.pewresearch.org/fact-tank/2019/09/11/key-findings-about-the-online-news-landscape-in-america/">“one-in-five adults said they often get news on social media.”</SpiceyLink> Both these statistics have risen from previous years and a trend is clear, the internet is playing an increasingly large role in the lives of Americans.
                </NormalText>
            </div>
</div>
            <SubTitle>The issue</SubTitle>
            <NormalText>
                The internet is being used. What's the big deal? The internet is still relatively new and the legal system is not currently equipped to handle free speech violations. Free speech regulations were created in a time where all impactful speech was done physically in public places. The only thing with the means to censor an individual was the government. Today, things have changed. We now gather on publicly accessible websites to express our views. Unlike their physical counterparts, these websites are privately owned and immune to most free speech protections.
            </NormalText>

            <SubTitle level={1}>Free speech concerns:</SubTitle>
            <SubTitle level={4}>Community Guideline Violations</SubTitle>
            <NormalText>
                Community Guidelines detail the types of content that can be posted to a given site. As these sites are private, they can pick and choose exactly what kind of content they will allow their users to share. These companies can update their community guidelines at any time and censor any kind of speech for violations.
            </NormalText>
            <SubTitle level={4}><kbd>“Shadow Banning”</kbd></SubTitle>
            <NormalText>
                You may have heard the term “shadow ban” <SpiceyLink href="https://www.cnet.com/news/shadow-banning-what-it-is-and-what-its-not/" noIFrame text="CNN article about shadow banning. Specifically, Trump's claims that Twitter was shadow banning republicans.">recently</SpiceyLink>. A shadow ban is a relatively new form of censorship that has arisen from social media sites. The basic idea is that your content won't be shown to an audience. The content will still exist on site, it simply won't be recommended to other users, even those who follow/subscribe to you. This all happens algorithmically so it is very difficult to prove this or rectify. Sites often shadow ban people who share views that the company does not want to be associated with or that the company does not approve of.
            </NormalText>
            <SubTitle level={2}>Things are in flux</SubTitle>
            <div class="row">
            <div className="col-8">
            <NormalText>
                Freedom of speech on social media platforms is currently under public scrutiny. Many conservatives voiced concerns over the apparent censorship of right-leaning views on major platforms. At the same time, democrats claim that <SpiceyLink href="https://www.nytimes.com/2020/10/12/technology/on-facebook-misinformation-is-more-popular-now-than-in-2016.html">misinformation on social media</SpiceyLink> played a role in the 2016 election. Earlier this year, President <SpiceyLink href="https://www.theverge.com/2020/5/28/21273822/trump-signs-executive-order-facebook-twitter-section-230-social-media-companies" noIFrame text={`Article from The Verge titled "Donald Trump signs executive order targeting social media companies"`}>Trump signed an executive order concerning speech on Twitter</SpiceyLink>. Things are changing and it’s now more important than ever to sort out who should be responsible for regulating speech on the internet.
            </NormalText>
            </div>
            <div className="col-4">
                <InArticleImage src="https://s.abcnews.com/images/Politics/trump-tax2-rt-ml-171222_16x9_1600.jpg" site="https://abcnews.go.com/Politics/president-trump-signs-tax-bill-law-leaving-holidays/story?id=51954035"/>
            </div>
            </div>
            <SubTitle level={2}>Who should do something about it?</SubTitle>
            <NormalText>This is the million-dollar question. The 3 major viewpoints this site will be addressing are:</NormalText>
            <SubTitle level={4}><SpiceyLink href="/the-people" sameSite>The People</SpiceyLink></SubTitle>
            <NormalText>
                Poor speech protections on social media is a reflection of the will of their users.
            </NormalText>
            <SubTitle level={4}><SpiceyLink href="/the-companies" sameSite>The Companies</SpiceyLink></SubTitle>
            <NormalText>
                We need social media giants to institute policies that protect speech on their platforms. 
            </NormalText>
            <SubTitle level={4}><SpiceyLink href="/the-government" sameSite>The Government</SpiceyLink></SubTitle>
            <NormalText>
                We need regulation explicitly granting freedom of speech protections to users on social media sites.
            </NormalText>
        </>
    )
}